@keyframes translate_Y {
	25%,
	100% {
		transform: translateY(0%);
	}
}

@keyframes rotate_Y {
	70% {
		transform: rotateY(0deg);
	}
	100% {
		transform: rotateY(180deg);
	}
}

@keyframes offset_top {
	20%, 40% {
		transform: translateY(-1vw);
	}
	10%, 30%, 50% {
		transform: translateY(0);
	}
}

@keyframes pulse {
	20%, 60%, 100% {
		transform: scale(1);
	}
	40%, 80% {
		transform: scale(2);
	}
}

@keyframes pulse-2 {
	20%, 60%, 100% {
		transform: scale(1.5);
	}
	40%, 80% {
		transform: scale(1.8);
	}
}

@keyframes arrow-anim {
	40%, 60%, 80% {
		transform: translateX(10px) translateY(-50%);
	}
}
