@font-face {
	font-display: swap;
	font-family: 'TTInterfaces';
	src: url('../fonts/TTInterfaces-Thin.eot');
	src: url('../fonts/TTInterfaces-Thin.eot?#iefix') format('embedded-opentype'),
	local('TTInterfaces-Thin'),
	url('../fonts/TTInterfaces-Thin.woff2') format('woff2'),
	url('../fonts/TTInterfaces-Thin.woff') format('woff'),
	url('../fonts/TTInterfaces-Thin.ttf') format('truetype');
	font-weight: 100;
	font-style: normal;
}

