html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  width: 0;
  height: 0;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden,
.link .visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

:focus {
  outline: none;
}

.hidden {
  height: 100vh;
  overflow: hidden;
}

html,
body {
    height: 100%;
}

body {
  min-width: 300px;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: $proxima;
  font-weight: 400;
  line-height: 1.6;
  font-size: 1vw;
  color: $color;
  background-color: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $gotham;
  line-height: 1.4;
  margin: 0;
}

h1 {
  font-weight: 900;
  font-size: 4vw;
  margin: ($offsetd * 2) 0;
}

h2 {
  font-weight: 900;
  font-size: 2.5vw;
}

h3 {
  font-weight: 900;
  font-size: 1vw;
}

h4 {
  text-transform: uppercase;
  font-weight: 900;
  font-size: 1.5vw;
}

h5 {
  font-weight: 500;
  font-size: 0.95vw;
  margin: $offsetd 0;
}

h6 {
  font-family: $proxima;
  text-transform: uppercase;
  font-weight: 900;
  font-size: 0.8vw;
}

p {
  margin-top: 1.5vw;
  margin-bottom: 1.5vw;
}

.reset {
  @include list-reset;
}

a {
  text-decoration: none;
  color: $color;
  transition: 0.1s linear;

  @include hoverFocus {
    color: $lightcolor;
  }
}

.link {
  position: relative;
  font-family: $proxima;
  font-weight: 700;
  font-size: 0.95vw;
  background-color: transparent;
  border: none;

  &::after {
    @include pseudo;
    bottom: 0;
    left: -0.3vw;
    width: calc(100% + 0.6vw);
    height: 0.8vw;
    transform: scaleX(0);
    transform-origin: left;
    background-color: rgba(25, 25, 25, 0.05);
    transition: 0.2s cubic-bezier(0.33, 0.5, 0.65, 0.49);
    z-index: -1;
  }

  @include hoverFocus {
    color: $color;

    &::after {
      transform: scaleY(1);
    }
  }
}

.btn {
  position: relative;
  display: inline-block;
  color: $color;
  border: 1px solid $lightcolor;
  background-color: transparent;
  font-family: inherit;
  font-weight: 700;
  font-size: 0.9vw;
  padding: ($offsetd / 2);
  min-width: 10vw;
  text-align: center;
  cursor: pointer;
  overflow: hidden;

  span {
    position: relative;
    transition: 0.2s ease-out;
    z-index: 1;
  }

  &::after {
    @include pseudo;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    transform: translateY(101%);
    background-color: $color;
    transition: 0.2s ease-out;
  }

  &--gray {
    border: none;
    background-color: rgba(25, 25, 25, 0.1);
  }

  &--disabled {
    pointer-events: none;
    color: $lightcolor;
  }

  @include hoverFocus {
    span {
      color: #fff;
    }

    &::after {
      transform: translateY(0);
    }
  }

  &--white {
    border: none;
    background-color: #ffffff;
    border-radius: 25px;

    &::after {
      background-color: #efefef;
    }

    @include hoverFocus {
      span {
        color: $color;
      }
    }
  }
}

.img,
img {
  width: 100%;
  height: auto;
}

.img {
  margin: ($offsetd * 2) 0;

  &--low {
    height: 20vw;
    overflow: hidden;

    img {
      height: 100%;
      object-fit: cover;
    }
  }
}

.descr {
  margin: ($offsetd * 2) 0;
}

.copyright_link {
  position: fixed;
  right: 5vw;
  bottom: $offsetd;
  display: flex;
  flex-direction: column;
  width: 2vw;
  height: 2vw;
  transition: .5s;
  transition-delay: .6s;
  overflow: hidden;
  margin-left: auto;
  z-index: 2;

  svg {
    width: 4.2vw;
    height: auto;
    opacity: 1;
    transform: none;
    top: auto;
    left: auto;
    fill: $color;
    stroke: $color;
    stroke-linecap: round;
  }

  .st0 {
    stroke-width: 12;
    stroke-linejoin: round;
    stroke-miterlimit: 10;
    margin-left: auto;
    animation: st0 2s linear 0s infinite normal;
  }

  .st1 {
    stroke-width: 4;
    stroke-linejoin: bevel;
    opacity: 0 !important;
    transition: .5s;
  }

  .st1 {
    transition-delay: .1s;
  }

  .st2 {
    transition-delay: .2s;
  }

  .st3 {
    transition-delay: .3s;
  }
}

.home {
  .copyright_link {
    svg {
      fill: #fff;
      stroke: #fff;
    }
  }
}

@media screen and (min-width: $tablet) {
  .copyright_link {
    @include hoverFocus {
      width: 4.428vw;
      transition-delay: 0s;
      overflow: visible;

      .st0 {
        animation: none
      }

      .st1 {
        opacity: 1 !important;
        transition-delay: .4s;
      }

      .st2 {
        transition-delay: .5s;
      }

      .st3 {
        transition-delay: .6s;
      }
    }
  }
}

@media screen and (max-width: $desktop) {
  body {
    font-size: 1.3vw;
  }

  h1 {
    font-size: 5vw;
  }

  h2 {
    font-size: 3.8vw;
  }

  h4 {
    font-size: 2vw;
  }

  h5 {
    font-size: 1.3vw;
  }

  h6 {
    font-size: 1.1vw;
  }

  p {
    margin-top: ($offsetd * 1.35);
    margin-bottom: ($offsetd * 1.35);
  }

  .title + span {
    font-size: 1.3vw;
  }

  .link {
    font-size: 1.3vw;
  }

  .btn {
    font-size: 1.2vw;
    min-width: 12vw;
    padding: ($offsetd / 1.5) $offsetd;
  }

  .copyright_link {
    right: 6.5vw;
    width: 2.7vw;

    svg {
      width: 5.5vw;
    }

    @include hoverFocus {
      width: 5.428vw;
    }
  }
}

@media screen and (max-width: $tablet-wide) {
  body {
    font-size: 15px;
  }

  h1 {
    font-size: 8vw;
  }

  h2 {
    font-size: 50px;
    line-height: 1;
  }

  h4 {
    font-size: 25px;
  }

  h5 {
    font-size: 16px;
    margin-top: $offsetm;
    margin-bottom: $offsetm;
  }

  h6 {
    font-size: 14px;
  }

  p {
    margin-top: $offsetm;
    margin-bottom: $offsetm;
  }

  .img {
    margin-top: ($offsetm * 2);
    margin-bottom: ($offsetm * 2);
  }

  .descr {
    margin-top: ($offsetm * 2);
    margin-bottom: ($offsetm * 2);
  }

  .link {
    font-size: 15px;

    &::after {
      left: -3px;
      width: calc(100% + 6px);
      height: 10px;
    }
  }

  .title + span {
    font-size: 15px;
    margin-bottom: ($offsetm * 2);
  }

  .btn {
    font-size: 14px;
    min-width: 140px;
    padding: ($offsetm / 1.5) $offsetm;
  }

  .copyright_link {
    right: calc(60px + 15px);
    bottom: $offsetm;
    width: 30px;
    height: 27px;

    svg {
      width: 64px;
    }

    @include hoverFocus {
      width: 65px;
    }
  }

}

@media screen and (max-width: $tablet) {
  .copyright_link {
    left: $offsetm;
    right: auto;
  }
}


@media screen and (max-width: $tablet-small) {
  .cases {
    .copyright_link {
      svg {
        fill: #fff;
        stroke: #fff;
      }
    }
  }
}

@media screen and (max-width: $phone-wide) {
  h2 {
    font-size: 10vw;
  }
}
