.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 4vw;
    bottom: 0;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s cubic-bezier(0.33, 0.5, 0.65, 0.49);
    z-index: 2;

    &[data-modal="showreal"] {
    	right: 0;
    }

    &.open {
    	opacity: 1;
    	visibility: visible;
    }
}

.modal_showreal {
	position: relative;
	width: 100%;
	height: 100%;

	.closed {
		left: $offsetd;
	}
}

.video {
	width: 100%;
	height: 100%;
	object-fit: contain;
	object-position: center;
	background-color: $color;
}

.video + .showreal {
	top: 0;
	left: 0;
    right: 4vw;
    width: 100%;
    height: 100%;
    cursor: pointer;

	.icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	    width: 5vw;
	    height: 5vw;
		margin-right: 0;
	    border-color: transparent;
	    opacity: 0;
	    transition: 0.2s ease-out;
	    filter: drop-shadow(0 0 5px rgba(25, 25, 25, 0.2));

	    &::after {
	    	content: none;
	    }
	}

	@include hoverFocus {
		.icon--play {
			opacity: 1;
		}
	}

	&.pause {
		@include hoverFocus {
			.icon--play {
				opacity: 0;
			}

			.icon--pause {
				opacity: 1;
			}
		}
	}

	svg {
		fill: #fff;
		width: 2.5vw;
		height: 2.5vw;
		margin-left: 0;
	}
}

.modal .closed {
	z-index: 3;

	svg {
		fill: #fff;
		filter: drop-shadow(0 0 5px rgba(25, 25, 25, 0.2));
	}
}


body {
	&.no-js {
		.header [data-modal] {
			display: none;
			overflow: hidden;
			opacity: 0;
		}
	}
}

@media screen and (max-width: $tablet-wide) {
	.modal_showreal {
		.closed {
			left: $offsetm;
		}
	}

	.video + .showreal {
		right: 60px;

		.icon {
			width: 60px;
		    height: 60px;
		}

		svg {
		    width: 40px;
		    height: 40px;
		}
	}

}
