body {
	&.not-found {
		background-color: #e3e3e3;

		.content_image {
			height: 70%;
		}

		.content_info h2 {
		    font-size: 11.5vw;
		    margin: 0;
		    line-height: 1;
		}

		.content_info h2 + span {
		    font-size: 2.25vw;
		}
	}
}

.wrapper {
	@include flex(row, space-between, stretch, nowrap);
	position: relative;
	height: 100vh;

	.anim {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #fff;
		z-index: 999;
		transition: opacity 0.5s ease-out;
		opacity: 0;
		visibility: hidden;

		&.start {
			opacity: 1;
			visibility: visible;
		}
	}

	.loaded {
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%) rotate(0deg);
		width: 3vw;
		height: 3vw;
		z-index: 999;

		span {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 100%;
			height: 100%;
			transform-origin: center;
			transform: translate(-50%, -50%) rotate(0deg);
			animation: rotate 1s linear reverse infinite;
		}
	}

	@keyframes rotate {
		100% {
			transform: translate(-50%, -50%) rotate(360deg);
		}
	}
}

.content {
	.anim {
		position: fixed;
		top: 0;
		left: auto;
		right: 4vw;
		bottom: 0;
		width: calc(60% + 4vw);
		transition: opacity 0.1s ease-out;
		opacity: 0;
		visibility: hidden;
	}

	.loaded {
		position: absolute;
	}
}

.loading {
	.content .anim {
		opacity: 1;
		background-color: rgba(255, 255, 255, 0.5);
		visibility: visible;
	}
}

.main {
	@include flex(row, space-between, stretch, nowrap);
	flex-shrink: 0;
	width: 100%;
	height: 100vh;
	padding-right: 4vw;
	overflow: hidden;
}

.aside {
	width: 33.3%;
	height: 100%;
	padding: 5vw ($offsetd * 3);
	overflow: auto;

	&::-webkit-scrollbar {
	  width: 2px;
	  height: 0;
	}

	&::-webkit-scrollbar-track {
	  background-color: #fff;
	}

	&::-webkit-scrollbar-thumb {
	  background-color: #000;
	}
}

.filter-wrapper {
	@include flex(column, center, flex-start, nowrap);
	min-height: 100%;
}

.filter {
	@include flex(column, flex-start, flex-start, nowrap);
	width: 100%;

	&.open {
		h5::after {
		    transform: scaleX(1);
		}

		.filter_title::after {
			transform: scaleX(1);
		}
	}
}

.filter_header {
	@include flex(row, space-between, center, nowrap);
	width: 100%;
	background: none;
	border: none;
	cursor: pointer;
	padding: 0;

	.filter_title {
		position: relative;
		font-family: $gotham;
		font-weight: 500;
		font-size: 0.95vw;
		margin: $offsetd 0;

		&::before {
			@include pseudo;
		    top: 60%;
		    left: -0.3vw;
		    width: calc(100% + 0.6vw);
		    height: 0.8vw;
		    background-color: rgba(25, 25, 25, 0.05);
		    z-index: -2;
		}

		&::after {
			@include pseudo;
		    top: 60%;
		    left: -0.3vw;
		    width: calc(100% + 0.6vw);
		    height: 0.8vw;
		    transform: scaleX(0);
		    transform-origin: left;
		    background-color: rgba(250, 12, 171, 0.25);
		    transition: 0.2s cubic-bezier(0.33, 0.5, 0.65, 0.49);
		    z-index: -1;
		}
	}

	.icon {
		position: relative;
		width: 0.8vw;
		height: 0.8vw;

		.line {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			background-color: $color;

			&:nth-of-type(1) {
				width: 100%;
				height: 3px;
			}

			&:nth-of-type(2) {
				width: 3px;
				height: 100%;
				transform: translate(-50%, -50%) rotate(90deg);
			}
		}
	}

	@include hoverFocus {
		color: $color;

		.filter_title::after {
			transform: scaleX(1);
		}
	}
}

.filter_inner {
	@include flex(row, space-between, center, wrap);
	width: 100%;
}

.filter_link {
	width: calc(50% - 2vw);
	margin-top: 0.4vw;
	margin-left: 1.5vw;
	margin-bottom: 0.4vw;
	color: $lightcolor;
	font-size: 0.95vw;
	line-height: 1;

	&.current {
		font-weight: 700;
		color: $color;
	}

	@include hoverFocus {
		font-weight: 700;
		color: $color;
	}
}

.content {
	@include flex(row, space-between, stretch, wrap);
	position: relative;
	width: 60%;
	height: 100%;
	flex-grow: 1;
	overflow: auto;

	&::-webkit-scrollbar {
	  width: 2px;
	  height: 0;
	}

	&::-webkit-scrollbar-track {
	  background-color: #fff;
	}

	&::-webkit-scrollbar-thumb {
	  background-color: #000;
	}
}

.content_header {
	@include flex(row, space-between, center, nowrap);
	width: 100%;
	height: 100vh;
	overflow: hidden;
}

.content_image {
	flex-shrink: 0;
	width: 50%;
	height: calc(80% - 3vw);
    margin-top: 3vw;
	margin-left: ($offsetd * 2);

	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		object-position: center;
        transition: 0.5s ease;
	}
}

.content_info {
	width: 40%;
	margin-right: ($offsetd * 2);

	h2 {
		font-size: 4vw;
		margin: ($offsetd * 2) 0 0;
	}

	.content_services {
		width: 100%;
	}

	.link {
	    @include flex(row, flex-start, center, nowrap);
		margin-top: ($offsetd * 2);

		&::after {
			content: none;
		}

		span:not(.arrow) {
			position: relative;
			color: $color;

			&::after {
				@include pseudo;
				left: -0.3vw;
				top: 50%;
				width: calc(100% + 0.6vw);
				height: 0.6vw;
				transform-origin: left;
				transform: scaleX(0);
				background-color: rgba(25, 25, 25, 0.05);
				transition: 0.2s ease-out;
			}
		}

		@include hoverFocus {
			span:not(.arrow) {
				&::after {
					transform: scaleX(1);
				}
			}

			.arrow {
				&::after {
					border-color: rgba(25, 25, 25, 0.1);
					transform: scale(0);
				}
			}
		}
	}

	.arrow {
	    @include flex(row, center, center, nowrap);
	    position: relative;
		margin-left: ($offsetd / 2);
		width: 2.5vw;
		height: 2.5vw;

		&::after {
			@include pseudo;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			border: 2px solid $color;
			border-radius: 50%;
			transform-origin: center;
			transition: 0.2s ease-out;
		}

		svg {
			width: 1vw;
			height: 1vw;
			transform: translateY(-100%);
			animation: translate_Y 2s cubic-bezier(0.4, 0, 1, 1) alternate infinite;
		}
	}
}

.content_wrap {
	opacity: 0;
	visibility: hidden;
	transition-delay: 0.7s;

	.closed {
	    position: fixed;
	    left: auto;
		right: calc(65% + 5vw);
		opacity: 0;
		visibility: hidden;
		transition: 0.2s ease-out 0s;
	}

	&.open {
		opacity: 1;
		visibility: visible;
		transition-delay: 0s;

		.overlay {
		    opacity: 1;
		    pointer-events: auto;
		}

		.content_section {
		    transform: translateX(0);
			transition: 0.7s ease-out;
		}

		.closed {
			opacity: 1;
			visibility: visible;
			transition-delay: 0.7s;
		}
	}
}

.content_section {
	position: fixed;
	top: 0;
	right: 4vw;
	width: 64%;
	height: 100%;
	padding: ($offsetd * 2.5);
	border-left: 2px solid rgba(25, 25, 25, 0.1);
	background-color: #fff;
	overflow: auto;
    transform: translateX(110%);
	transition: 0.7s ease-in;

	&::-webkit-scrollbar {
	  width: 2px;
	  height: 0;
	}

	&::-webkit-scrollbar-track {
	  background-color: #fff;
	}

	&::-webkit-scrollbar-thumb {
	  background-color: #000;
	}

	h1 {
		width: 100%;
		margin-left: auto;
	}
}

.content_services {
	@include flex(row, flex-start, flex-start, nowrap);
	width: 100%;
	margin-left: auto;
	font-size: 0.85vw;

	&-left {
		margin-right: ($offsetd * 2.5);
	}

	h6 {
		margin-bottom: 0.5vw;
	}

	li {
		margin-top: 0.25vw;
		margin-bottom: 0.25vw;
		color: $lightcolor;
	}
}

.content_main {
	border-bottom: 1px solid rgba(25, 25, 25, 0.1);
}

.content_bottom {
	@include flex(row, space-between, center, nowrap);
	padding-top: ($offsetd * 2);
	padding-bottom: ($offsetd * 2);

	.prev,
	.next {
		@include flex(row, flex-start, center, nowrap);
		width: 40%;

		.link_inner:first-child,
		span:first-child {
			margin-right: $offsetd;
		}

		.icon {
			@include flex(row, center, center, nowrap);
			position: relative;
		    width: 2vw;
		    height: 2vw;
		    border-radius: 50%;

		    &::before {
		    	@include pseudo;
		    	width: 100%;
		    	height: 100%;
		    	transform: scale(0);
		    	border-radius: 50%;
		    	background-color: #fff;
		    	transition: 0.2s ease-out;
		    }
		}

		svg {
			position: relative;
			fill: $color;
			transition: 0.2s ease-out;
			z-index: 1;
		}

		@include hoverFocus {
			.icon::before {
		    	transform: scale(1);
				background-color: $color;
			}

			.link_inner span {
				color: $color;
			}

			svg {
				fill: #fff;
			}
		}
	}

	.prev {
		svg {
			transform: rotate(90deg);
		}
	}

	.next {
		justify-content: flex-end;
		text-align: right;

		svg {
			transform: rotate(270deg);
		}
	}
}

.slider {
    width: 100%;
    overflow: hidden;
}

.slick-track {
	@include flex(row, flex-start, stretch, nowrap);
}

.slick-dots {
	@include flex(row, center, center, nowrap);
	@include list-reset;
	margin-top: ($offsetd * 2);

	li {
		@include flex(row, center, center, nowrap);
		flex-shrink: 0;
		width: 0.75vw;
		height: 0.75vw;
		margin: 0 ($offsetd / 6);
		transform: scale(0.7);
		transition: 0.2s ease-out;

		&.slick-active {
			transform: scale(1);

			button {
				background-color: $color;
			}
		}

		@include hoverFocus {
			transform: scale(1);
		}
	}

	button {
		display: block;
		width: 100%;
		height: 100%;
		padding: 0;
		background-color: rgba(25, 25, 25, 0.2);
		border-radius: 50%;
		border: none;
		font-size: 0;
		transition: 0.2s ease-out;
		cursor: pointer;
	}
}

.link_inner {
	h5 {
		margin: 0;
	}

	span {
		color: $lightcolor;
		font-size: 0.85vw;
		transition: 0.2s ease-out;
	}
}

.link_work {
	width: 2vw;
	height: 2vw;

	.icon {
		@include flex(row, space-between, center, wrap);
		width: 100%;
		height: 100%;
	}

	.squaer {
		width: calc(50% - 0.15vw);
		height: calc(50% - 0.25vw);
		border: 3px solid $color;
		transition: 0.2s ease-out;
	}

	@include hoverFocus {
		.squaer {
			&:nth-of-type(1) {
				transform: translate(0.1vw, 0.1vw);
			}
			&:nth-of-type(2) {
				transform: translate(-0.1vw, 0.1vw);
			}
			&:nth-of-type(3) {
				transform: translate(0.1vw, -0.05vw);
			}
			&:nth-of-type(4) {
				transform: translate(-0.1vw, -0.05vw);
			}
		}
	}
}

.case {
	@include flex(column, center, stretch, nowrap);
	position: relative;
	width: 33.3%;
	height: 50vh;
	min-height: 20vw;
	padding: $offsetd;
	border-left: 1px solid rgba(25, 25, 25, 0.1);
	border-bottom: 1px solid rgba(25, 25, 25, 0.1);
	text-align: center;
	overflow: hidden;

	a {
		@include flex(column, center, stretch, nowrap);
		position: relative;
		width: 100%;
		height: 100%;
		max-height: 27vw;
	    min-height: 20vw;
		z-index: 1;
	}

	&--order {
		&::before {
			@include pseudo;
			top: 45%;
			left: 50%;
			transform: translate(-50%, -50%);
            transform-origin: center center;
			width: 18vw;
			height: 18vw;
            border-radius: 50%;
			background-color: #93dcff;
			transition: 0.25s cubic-bezier(0.33, 0.5, 0.65, 0.49);
		}

		&-1::before {
			background-color: #93c8ff;
		}

		&-2::before {
			background-color: #f2b9de;
		}

		&-3::before {
			background-color: #fec3b1;
		}

		&-4::before {
			background-color: #93afff;
		}

		&-5::before {
			background-color: #d7b0fa;
		}

		a {
			justify-content: center;

			.case_header {
				position: relative;
				top: auto;
				left: auto;
				margin-top: auto;
				padding: $offsetd;
			}
		}
	}

	@include hoverFocus {
		&::before {
			top: 50%;
			transform: translate(-50%, -50%) scale(2.25);
		}

		.case_category {
			color: #fff;
		}

        .case_image {
            transform: scale(0.7);
            filter: brightness(0.9) contrast(1.2);
        }
	}
}

.case_header {
	position: absolute;
    top: ($offsetd * 3);
    left: 0;
    width: 100%;
    z-index: 2;
}

.case_title {
	margin-bottom: $offsetd;
	font-weight: 600;
	font-size: 1.5vw;
	color: #fff;
}

.case_image {
	position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 30px rgba(0,0,0,0.1);
    transition: 0.15s cubic-bezier(0.33, 0.5, 0.65, 0.49);
    z-index: 1;

    img {
    	width: 100%;
    	height: 100%;
    	object-fit: cover;
    	object-position: center;
    }
}

.case_footer {
	@include flex(row, center, center, wrap);
	margin-top: auto;
}

.case_category {
	@include flex(row, center, center, nowrap);
	color: #ffffff;
	font-size: 0.85vw;
	transition: 0.1s cubic-bezier(0.33, 0.5, 0.65, 0.49);
	z-index: 1;

	&::after {
		@include pseudo($pos: relative, $content: "/");
		margin-left: 0.5vw;
		margin-right: 0.5vw;
		font-size: 0.85vw;
	}

	&:last-of-type::after {
		content: none;
	}
}

.history {
	@include flex(row, space-between, stretch, wrap);
	margin-top: ($offsetd * 2);
}

.history_item {
	position: relative;
	width: 50%;
	margin-bottom: ($offsetd * 2);
	padding: 0 ($offsetd * 2);
	border-left: 2px solid rgba(25, 25, 25, 0.05);

	&:nth-last-of-type(2),
	&:last-of-type {
		margin-bottom: 0;
	}

	&::after {
	    @include pseudo;
	    top: 50%;
	    left: 0;
	    width: 70%;
	    height: 80%;
	    background-color: rgba(25, 25, 25, 0.02);
	    transform: scaleX(0) translateY(-50%);
	    transform-origin: left;
	    transition: 0.3s ease-out;
	}

	.caption {
		margin-bottom: 0;
	}

	@include hoverFocus {
		&::after {
		    transform: scaleX(1) translateY(-50%);
		}
	}
}

.date {
	font-family: $gotham;
	font-weight: 900;
}

.contact_row {
	@include flex(column, flex-start, flex-start, nowrap);
	margin-bottom: $offsetd;

	.link {
		position: relative;
		margin-top: 0;

		&::after {
			@include pseudo;
		    left: -0.3vw;
		    top: 50%;
		    width: calc(100% + 0.6vw);
		    height: 0.8vw;
		    transform: scaleX(0);
		    transform-origin: left;
		    background-color: rgba(25, 25, 25, 0.05);
		    transition: 0.2s cubic-bezier(0.33, 0.5, 0.65, 0.49);
		}

		@include hoverFocus {
			color: $color;

			&::after {
				transform: scaleX(1);
			}
		}
	}
}

.contact_col {
	@include flex(row, flex-start, flex-start, nowrap);

	a {
		margin-top: ($offsetd / 2.5);
		margin-right: $offsetd;
		transition: 0.2s ease-out;

		.icon {
			display: block;
		    width: 1.2vw;
		    height: 1.2vw;
		}

		svg {
			fill: $color;
			width: 100%;
			height: 100%;
			transition: 0.3s ease-out;
		}

		@include hoverFocus {
			transform: scale(1.5);
		}
	}
}

.map {
	width: 70%;
	height: 100vh;
}

.home {
	.slider {
		width: calc(100% + 4vw);
	    overflow: visible;
		z-index: 1;
	}

	.slick-arrow {
		position: absolute;
		top: 50%;
		left: ($offsetd * 1.5);
		width: 3vw;
		height: 3vw;
		background-color: transparent;
		border: none;
		border-radius: 50%;
		cursor: pointer;
		font-size: 0;
		z-index: 1000;

		&::before {
			@include pseudo;
			top: 50%;
			left: 50%;
		    width: 100%;
		    height: 100%;
		    margin-left: -50%;
		    margin-top: -50%;
	        background-color: rgba(255, 255, 255, 0.1);
		    border-radius: 50%;
		    transform: scale(1.5);
		    z-index: -1;
		}

		&::after {
			@include pseudo;
			top: 50%;
			left: 50%;
			width: 25%;
			height: 25%;
			transform: translate(-50%, -50%) rotate(45deg);
		}

		&.slick-prev {
			margin-top: -2.6vw;

			&::after {
				left: 55%;
				border-left: 2px solid #fff;
				border-bottom: 2px solid #fff;
			}
		}

		&.slick-next {
			margin-top: 2.6vw;

			&::after {
				left: 45%;
				border-top: 2px solid #fff;
				border-right: 2px solid #fff;
			}
		}

		@include hoverFocus {
			&::before {
				animation: pulse-2 2s ease-out infinite;
			}
		}
	}

    .slick-dots {
        position: fixed;
        left: (1.5vw + ($offsetd * 3.3));
        bottom: ($offsetd * 1.5);
        z-index: 999;

        li {
            width: 10px;
            height: 10px;
            margin-left: 0;
            margin-right: ($offsetd);
            transform: none;
            opacity: 0.5;
            transition: 0.15s ease-in;
        }

        button {
          background-color: #ffffff;
        }

        li.slick-active {
            transform: none;
            opacity: 1;

            button {
                background-color: #ffffff;
            }
        }
    }

    .content_nav {
      margin-top: $offsetd;
      margin-bottom: ($offsetd * 2);
      padding-right: ($offsetd * 4);

      button {
        position: relative;
        font-family: inherit;
        font-size: 1.5vw;
        padding: 0;
        color: rgba(255,255,255,0.5);
        border: none;
        background-color: transparent;
        cursor: pointer;
        transition: 0.5s ease;
        white-space: nowrap;

        &:not(:last-of-type)::after {
          @include pseudo($display: 'inline', $pos: relative, $content: ', ');
          font-size: inherit;
          margin-left: -4px;
        }

        span {
          font-size: inherit;
        }

        &.active {
          color: rgba(255,255,255,1);
          //text-shadow: 0 0 5px rgba(255,255,255,0.5);
        }
      }
    }

	.content_header {
		display: flex !important;
		position: relative;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		height: 100vh;
		opacity: 1 !important;

		.content_bg {
			@include pseudo;
			text-transform: uppercase;
			font-family: $gotham;
			white-space: nowrap;
			font-weight: 900;
			font-size: 9.5vw;
			color: rgba(255,255,255,0.2);
			text-align: center;
			line-height: 100vh;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #32adfe;
			transform-origin: right;
			transform: scaleX(0);
			transition: all 0s cubic-bezier(1, 0.71, 0.52, 1) 1s, color 0s 1s;
			z-index: -1;
		}

		&:nth-of-type(2) {
			&::before {
				background-color: #2184ff;
			}
		}

		&.slick-active {
			.content_bg {
				transform: scaleX(1);
				transition-duration: 1s;
				transition-delay: 0s;
			}

			&::after {
				opacity: 1;
			}

			.content_info {
				&:last-of-type {
					opacity: 1;
					transform: translateX(0);
				}

				h2,
				span {
					opacity: 1;
				}
			}


			.content_image {
				opacity: 1;
				transform: translateX(0);
			}
		}
	}

	.content_image {
		width: 50%;
		height: calc(100% - 10vw);
		margin-top: 10vw;
	    margin-left: auto;
	    margin-right: 15vw;
		opacity: 0;
		transform: translateX(-2vw);
		transition: 0.5s cubic-bezier(1, 1, 0.55, 1) 0.5s;

        img {
          opacity: 0;
          transform: translateY(50%);
        }
	}

	.content_info {
        position: fixed;
        top: 50%;
        left: (1.5vw + ($offsetd * 3));
        width: 22%;
		z-index: 2;
		color: #fff;
        opacity: 0;
        transform: translateY(0);
        transition: 0.5s ease-out;

		h2 {
            margin-top: 0;
			font-family: $proxima;
			font-size: 3vw;
            line-height: 1;
		}

		span {
			font-weight: 400;
			font-size: 0.8vw;
		}

        .btn span {
            font-weight: 600;
        }
	}

    &.loaded {
        .content_image {
            img {
                opacity: 1;
                transform: translateY(0);
            }
        }

        .content_info {
            opacity: 1;
            transform: translateY(-50%);
        }
    }
}

