body {
	& > svg:nth-of-type(1) {
		display: none;
	}

	&.no-js {
		.content_wrap {
			opacity: 1;
			visibility: visible;

			.closed {
				display: none;
			}
		}

		.content_section {
			position: relative;
			top: auto;
			right: auto;
			transform: none;
			width: 100%;
		}

		.overlay {
			display: none;
		}

		.slider {
			@include flex(row, flex-start, center, nowrap);
		    overflow: auto;

		    &::-webkit-scrollbar {
			  width: 2px;
			  height: 5px;
			}

			&::-webkit-scrollbar-track {
			  background-color: rgba(25, 25, 25, 0.1);
			}

			&::-webkit-scrollbar-thumb {
			  background-color: #000;
			}

			.slider_item {
				flex-shrink: 0;
				margin-bottom: ($offsetd * 1.5);
			}
		}

		.home {
			.content_header {
				flex-shrink: 0;

				&::before {
					transform: scaleX(1);
				}

				&::after {
				    opacity: 1;
				}
			}

			.content_info {
				&:last-of-type {
					opacity: 1;
					transform: translateX(0);
				}

				h2,
				span {
					opacity: 1;
				}
			}

			.content_image {
				opacity: 1;
			    transform: translateX(0);
			}

			.slider {
				flex-direction: column;
			}
		}
	}

	&:not(.no-js) {
		.animate,
		.content_section p {
			display: block;
			transform: translateY(5vw) skew(25deg);
			opacity: 0;
			transition: transform 0.5s ease-out, opacity 0.2s ease-out;

			&--img {
				position: relative;
				transform: none;
				overflow: hidden;

				&::after {
					@include pseudo;
					top: 0;
					left: -110%;
					width: 100%;
					height: 100%;
					background-color: rgba(25, 25, 25, 0.1);
					transition: 0.7s ease-out;
				}

				img {
					opacity: 0;
					transition: all 0.15s ease-out, opacity 0.3s ease-out 0.7s;
				}
			}

			&.active {
				transform: translateY(0) skew(0deg);
				opacity: 1;
			}

			&--img.active {
				&::after {
					left: 110%;
				}

				img {
					opacity: 1;
				}
			}
		}

		.filter {
			.filter_header .icon .line:nth-of-type(2) {
				transform: translate(-50%, -50%) rotate(0deg);
			}

			&.open {
				.filter_header .icon .line:nth-of-type(2) {
					transform: translate(-50%, -50%) rotate(90deg);
				}
			}
		}
	}
}

