				}

				.arrow {
					margin-top: $offsetm;
					margin-left: 0;
				}
			}

			.content_image {
				position: relative;
			    width: 100%;
				height: calc(100vh - 120px);
				overflow: hidden;
			    margin-left: 0;

			    &::before {
			    	@include pseudo($content: 'not found');
			    	top: 47%;
				    left: -4vw;
			    	font-family: $gotham;
				    font-weight: 900;
		    	    font-size: 9vh;
		    	    text-transform: uppercase;
		    	    writing-mode: vertical-lr;
		    	    white-space: nowrap;
		    	    transform: translateY(-50%) scale(-1);
				    color: rgba(255, 255, 255, 1);
			        text-shadow: 0 -4px 0 rgba(25, 25, 25, 0.1);
			        z-index: 1;
			    }

			    &::after {
			    	@include pseudo($content: '404');
				    top: 35%;
				    right: ($offsetm * 2.5);
			    	font-family: $gotham;
				    font-weight: 900;
		    	    font-size: 19vw;
				    transform: translateY(-50%) rotate(385deg);
				    color: rgba(25, 25, 25, 0.1);
			    }
			}
		}
	}

	.wrapper {
	    width: 100%;
	    overflow: hidden;
	}

	.main {
		padding-right: 0;
	}

	.aside {
		width: 300px;
		height: auto;
		margin-top: ($offsetm * 4);
		margin-bottom: ($offsetm * 4);
	}

	.content {
		height: auto;
		margin-top: ($offsetm * 4);
		margin-bottom: ($offsetm * 4);
	}

	.case {
		width: 100%;
		height: 100%;

		&--order {
			.case_footer {
				margin-bottom: ($offsetm * 2);
			}
		}

        @include hoverFocus {
           .case_image {
               transform: none;
           }
        }
	}

	.case_header {
		top: 30px;
	}

	.case_title {
	    font-size: 6vw;
	    line-height: 1.2;
	}

	.content_header {
		flex-direction: column;
		height: auto;
		overflow: visible;
	}

	.content_info {
		h2 {
			margin-top: 0;
		}
	}

	.history_item {
		width: 100%;
		padding-right: 0;
	}

	.home {
		.slider {
			width: 100%;
		}

        .slick-dots {
            display: none !important;
        }

		.content_header {
			padding-top: ($offsetm * 7);
			padding-bottom: ($offsetm * 5);

			&::before {
				line-height: 90vh;
			}

			&.slick-active .content_info:first-of-type {
			    opacity: 1;
			    transform: translateX(0);
			}
		}

		.content_image {
            margin-right: ($offsetm * 2);
		}
	}

	.about {
		.content_header {
			height: auto;
			min-height: 380px;
			padding: 0 ($offsetm * 4);
		}

		.content_image {
			order: 1;
            width: 100%;
            height: auto;
            margin-bottom: ($offsetm * 2);
			transform: scaleX(-1);
		}

		.content_info {
			width: 100%;
            margin-top: ($offsetm * 2);
			margin-right: 0;
		}
	}

	.case-inner {
		.content_header {
			padding-top: ($offsetm * 2);
			padding-left: ($offsetm * 4);
			padding-right: ($offsetm * 4);

			.content_services {
				display: none;
			}
		}

		.content_image,
		.content_info {
			width: 100%;
			margin: 0;
		}

		.content_image {
			height: auto;
            margin-top: ($offsetm * 2);
            margin-bottom: ($offsetm * 2);
		}

		.content_info {
			order: -1;

			h2 {
				margin-top: $offsetm;
			}

			.link[data-opened="all-content"] {
				display: none;
			}
		}

		.content_services {
			width: 100%;

			li {
				margin-top: ($offsetm / 2);
				margin-bottom: ($offsetm / 2);
			}
		}

		.content_wrap {
			width: 100%;
			opacity: 1;
			visibility: visible;

			.overlay {
				display: none;
			}

			.closed {
				display: none;
			}
		}

		.content_section {
			position: relative;
			width: 100%;
			height: auto;
            padding-bottom: 0;
			top: auto;
			right: auto;
			transform: none;
			border-left: none;

			h1 {
				display: none;
			}
		}
	}

	.contact {
		.content_header {
			margin-top: ($offsetm * 4);
			margin-bottom: ($offsetm * 2);
			padding: ($offsetm * 2) ($offsetm * 4) ($offsetm * 4);
			overflow: auto;

			&::-webkit-scrollbar {
			  width: 2px;
			  height: 0;
			}

			&::-webkit-scrollbar-track {
			  background-color: #fff;
			}

			&::-webkit-scrollbar-thumb {
			  background-color: #000;
			}
		}

		.content_info {
			position: relative;
			width: 100%;
			margin: 0;
		}

		.map {
			flex-shrink: 0;
			height: 50vh;
		}
	}
}

@media screen and (min-width: 641px) {
	.aside-opened {
		display: none;
	}
}

@media screen and (max-width: $tablet-small) {
	body {
		&.not-found {
			.content_info {
				width: 100%;
				margin-bottom: ($offsetm * 4) * 2;
				padding-left: ($offsetm * 3);
				padding-right: ($offsetm * 3);
			}

			.content_image {
				height: 100vh;
			}
		}
	}

	.aside-opened {
		position: absolute;
		top: 50%;
		left: $offsetm;
		transform: translateY(-50%);
		background: none;
		border: none;
	    font-size: 17px;
	    padding: 0;
		z-index: 2;

		span {
			font-family: $gotham;
			text-transform: uppercase;
			font-weight: 600;
			color: #fff;
		    writing-mode: vertical-lr;
		    cursor: pointer;
		    transition: 0.3s ease-out;
		}
	}

	.aside {
		width: 100%;
		flex-shrink: 0;
		margin-left: -100%;
		transition: 0.3s ease-out;
	}

	.open-filter {
		.aside {
			margin-left: 0;
		}
	}

	.opener,
	.link_info,
	.lang,
	.copyright_link svg {
		transition: 0.3s ease-out;
	}

	.content {
		flex-shrink: 0;
		width: 90%;
		margin-top: 0;
		margin-bottom: 0;
	}

	.case {
        height: auto;
        margin-top: -8px;
        padding: 0;

        a {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            max-height: none;
        }

		&--order {
			.case_title {
				font-size: 8vw;
			}

            .case_footer {
                margin-top: 30px;
                margin-bottom: auto;
            }
		}

		@include hoverFocus {
            .case_image {
                transform: none;
                filter: none;
            }
		}
	}

    .case_image {
      position: relative;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto;
      height: auto;
      box-shadow: none;

      img {
        height: auto;
      }
    }

	.case_title {
		font-size: 10vw;
	}

	.open-filter {
		.logo,
		.opener,
		.link_info,
		.lang,
		.copyright_link svg {
			filter: brightness(0) invert(0);
		}

		.aside-opened {
			span {
				color: $color;
			}
		}
	}

	.content_bottom {
		padding-bottom: ($offsetm * 2);

		.prev,
		.next {
			width: 47%;
		}

		.link_work {
			display: none !important;
		}
	}

    .home {
        .content_info {
            left: 50%;
            transform: translate(-50%, -50%) !important;
            width: auto;
            margin-right: 0;
            text-align: center;
        }

        .content_image {
            display: none;
        }
    }

	.about {
        .content_section {
          padding-bottom: ($offsetm * 4);
        }

		.content_header {
			flex-direction: column;
			align-items: flex-start;
			padding-top: ($offsetm * 6);
			padding-bottom: ($offsetm * 4);
		}

		.content_image {
			width: 100%;
			transform: none;
			margin-top: ($offsetm * 2);
			margin-right: 0;
            margin-bottom: 0;
		}

		.content_info {
			width: 100%;
		}
	}

	.case-inner {
		.content {
			margin-top: ($offsetm * 4);
			margin-bottom: ($offsetm * 4);
		}

		.content_header {
			padding-top: ($offsetm * 2);
			padding-bottom: ($offsetm * 4);
		}

		.content_section {
			padding-top: 0;
		}
	}

	.sitemap {
		&.main {
            height: calc(100vh - 120px);
            margin-top: ($offsetm * 4);
            margin-bottom: ($offsetm * 4);
        }

		.filter_link {
			width: 100%;
		}
	}
}

@media screen and (max-width: $phablet) {
	.content_bottom {
		padding-bottom: ($offsetm * 2);
		flex-direction: column;
		justify-content: flex-start;
		align-items: flex-start;

		.prev,
		.next {
			justify-content: space-between;
			width: 100%;
		    flex-shrink: 0;
		}

		.prev,
		.next {
			.icon {
				order: 1;
				margin-left: $offsetm;
				margin-right: 0 !important;
			}

			svg {
				transform: rotate(270deg);
			}
		}

		.next {
			text-align: left;
			margin-top: $offsetm;
		}
	}
}

@media screen and (max-width: $phone-wide) {
	body {
		&.not-found {
			.content_image {
				img {
					transform: scale(1.5);
				}
			}
		}
	}

	.history_item {
		padding-left: 0;
		border-left: none;
	}

	.content_info {
		.link {
			margin-top: 0;
		}
	}

	.content_bottom {
		padding-bottom: $offsetm;
	}

	.about {
		.content {
			margin-top: ($offsetm * 4);
			margin-bottom: ($offsetm * 4);
		}

		.content_header {
			min-height: calc(100vh - 120px);
			padding-left: $offsetm;
			padding-right: $offsetm;
		}

		.content_info {
			margin-top: -($offsetm * 4);
		}

		.content_section {
			padding-left: $offsetm;
			padding-right: $offsetm;
            padding-bottom: 0;
		}

		.slider--client {
			a {
				width: 75%;
				height: 120px;
			}
		}
	}

	.case-inner {
		.content_header,
		.content_section {
			padding-left: $offsetm;
			padding-right: $offsetm;
		}

		.content_header {
			padding-top: 0;
			padding-bottom: 0;
		}
	}

	.contact {
		.content_header {
			padding-left: $offsetm;
			padding-right: $offsetm;
		}
	}
}
