.header {
	position: relative;
	z-index: 3;
}

.logo {
	position: fixed;
	top: $offsetd;
	left: $offsetd;
	width: 1.5vw;
    height: 2vw;
    filter: brightness(0) invert(0);
    transition: 0.05s cubic-bezier(0.33, 0.5, 0.65, 0.49);
    overflow: hidden;

    @include hoverFocus {
        transform: scale(1.1);
    }

	img {
	    width: 1.5vw;
	    height: 2vw;
		object-fit: contain;
		object-position: left;
	}
}

.menu_list {
    position: fixed;
    top: $offsetd;
    left: (1.5vw + ($offsetd * 3));
    @include flex(row, flex-start, center, nowrap);
    height: 2vw;
    @include list-reset;

    .menu_item {
        margin-right: $offsetd;
    }

    a {
        font-family: $proxima;
        font-weight: 400;
        font-size: .95vw;
    }
}

.sitemap {
    .menu_list {
        display: none;
    }
}

.nav {
	position: relative;
}

.overlay {
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.25);
    opacity: 0;
    pointer-events: none;
    transition: 0.1s ease-out;
    cursor: pointer;
}

.nav_header {
	@include flex(column, space-between, center, wrap);
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	width: 4vw;
	z-index: 3;
	box-shadow: -1px 0 3px rgba(25, 25, 25, 0.1);
	transition: 0.1s ease-in;
	transition-delay: 0.7s;
}

.nav_section {
	@include flex(row, flex-start, stretch, nowrap);
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	width: 68%;
	height: 100%;
	transform: translateX(105%);
	transition: 0.7s;
	transition-delay: 0.3s;
	transition-timing-function: ease-in;
	z-index: 2;
}

.nav_about,
.nav_form,
.nav_menu {
	@include flex(column, center, space-around, nowrap);
	position: relative;
	width: calc(50% - 2vw);
	height: 100%;
	padding: ($offsetd * 2.5) ($offsetd * 2.5) ($offsetd * 5);
	flex-shrink: 0;
	background-color: #fff;
	border-left: 2px solid rgba(25, 25, 25, 0.1);
}

.nav_about {
	transform: translateX(101%);
	transition: 0.35s ease-out;
	transition-delay: 0s;
}

.nav_form {
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	transition: 0.2s ease-out;
}

.nav_row {
	width: 100%;

	&--fixed-bottom {
		@include flex(row, flex-start, center, wrap);
		position: absolute;
		left: 0;
		right: 0;
		bottom: 0;
		padding: $offsetd ($offsetd * 2.5) ($offsetd * 1.5);
	}
}

.nav_column {
	@include flex(column, flex-start, flex-start, nowrap);
	width: 50%;
	padding-right: $offsetd;
}

.nav_descr {
  font-family: $proxima;
  font-weight: 600;
  margin-bottom: 0.3vw;
  color: rgba(25, 25, 25, 0.3);
}

.menu_link {
	@include flex(column, flex-start, flex-start, nowrap);
	padding-top: ($offsetd / 1.5);
	padding-bottom: ($offsetd / 1.5);

	& > span {
		transition: color 0.2s ease-out, transform 0.5s ease-out 0.2s;
		transform-origin: left;
	}

	@include hoverFocus {
		color: $color;

		.menu_title::after {
			transform: scaleX(1);
		}
	}
}

.menu_title {
	@include flex(row, flex-start, flex-end, wrap);
	position: relative;
	font-family: $gotham;
	font-weight: 900;
	line-height: 1;
	font-size: 3.8vw;

	&::after {
		@include pseudo;
	    right: -0.5vw;
	    bottom: 0;
	    width: calc(100% + 1vw);
	    height: 0.8vw;
	    transform: scaleX(0);
	    transform-origin: left;
		background-color: rgba(25, 25, 25, 0.05);
		transition: 0.2s cubic-bezier(0.33, 0.5, 0.65, 0.49);
	}
}

.menu_subtitle {
	color: rgba(25, 25, 25, 0.5);
}

.title + span {
	text-transform: uppercase;
	font-weight: 600;
	font-size: 0.9vw;
	display: block;
	margin-bottom: $offsetd;
}

.opener {
    @include flex(row, center, center, wrap);
    position: relative;
    width: 25px;
    height: 20px;
    margin-top: $offsetd;
    z-index: 3;

    .line {
	    position: absolute;
	    left: 50%;
	    width: 100%;
	    height: 3px;
	    transform: translate(-50%, 0);
	    background-color: $color;
	    display: block;
	    flex-shrink: 0;
	    transition: 0.1s ease-in;

	    &:nth-of-type(2) {
	    	top: 0;
	    }

	    &:nth-of-type(3) {
	    	top: 50%;
	    	margin-top: -1.5px;
	    }

	    &:nth-of-type(4) {
	    	bottom: 0;
	    }
	}

	@include hoverFocus {
	    .line {
	    	width: 80%;
	    }
	}
}

.closed {
	position: absolute;
	top: $offsetd;
	left: $offsetd * 2.5;
	padding: 0;
	background-color: transparent;
	border: none;
	cursor: pointer;

	svg {
		width: 1.5vw;
		height: 1.5vw;
		transform: scaleX(-1);
		transition: 0.2s;
		transition-timing-function: ease-out;
	}

	@include hoverFocus {
		svg {
			transform: scaleX(-1) rotate(180deg);
			transition-timing-function: ease-in;
		}
	}
}

.lang {
	@include flex(column, flex-end, center, nowrap);
	position: absolute;
	left: 50%;
	bottom: $offsetd;
}

.lang-link {
	text-transform: uppercase;
	flex-shrink: 0;
	transform: translateX(-50%);
	font-family: $gotham;
	font-weight: 500;
	font-size: 0.9vw;
	margin-top: ($offsetd / 1.5);
	line-height: 1;
	pointer-events: auto;
	opacity: 1;
	transition: color 0.2s ease-out, opacity 0.2s ease-out 0.2s;

	&--active {
		font-weight: 900;
		cursor: default;
	}

	@include hoverFocus {
		color: $color;
		font-weight: 900;
	}
}

.social {
	@include flex(column-reverse, flex-end, center, nowrap);
	margin-top: auto;
	margin-bottom: $offsetd;
}

.social_item {
	flex-shrink: 0;

	&:nth-of-type(1) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(2) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(3) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(4) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(5) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(6) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(7) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(8) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(9) .social_link {
		transition-delay: 0s;
	}

	&:nth-of-type(10) .social_link {
		transition-delay: 0s;
	}
}

.social_link {
	display: block;
	width: 1.2vw;
	height: 1.2vw;
	margin: ($offsetd) ($offsetd / 2);
	pointer-events: none;
	opacity: 0;
	transform: translateX(2vw);
	transition: 0.2s ease-out;

	span {
		width: 100%;
		height: 100%;
	}

	svg {
		width: 100%;
		height: 100%;
		fill: rgba(25, 25, 25, 0.3);
		transition: 0.2s ease-out;
	}

	@include hoverFocus {
		svg {
			fill: $color;
		}
	}
}

.map-link {
	font-family: $gotham;
	font-weight: 700;
	font-size: 0.9vw;
	width: 1vw;
	margin-bottom: $offsetd;
	writing-mode: vertical-lr;
	pointer-events: none;
	opacity: 0;
	transform: translateX(1vw) scale(-1);
	transition: color 0.2s ease-out, opacity 0.2s ease-out 0.2s, transform 0.2s ease-out 0.2s;
}

.home {
	.logo {
		filter: brightness(0) invert(1);
	}

    .menu_list {
        a {
            color: #ffffff;
        }
    }

	.opener {
		.line {
			background-color: #fff;
		}
	}

	.lang-link {
		color: #fff;
	}
}

.visible-elem {
    visibility: visible;
}

.hidden-elem {
    visibility: hidden;
}

.open-menu {
	.nav_header {
		background-color: #fff;
		transition-timing-function: ease-out;
		transition-delay: 0s;
	}

	.nav_section {
		transform: translateX(0);
		transition-delay: 0s;
		transition-timing-function: ease-out;
	}

	.overlay {
		opacity: 1;
	    pointer-events: auto;
	}

	.nav_about {
		transform: translateX(0);
		transition-delay: 0.6s;
	}

	.opener {
		.line {
			background-color: $color;

		    &:nth-of-type(2) {
		    	top: 50%;
		    	margin-top: -1.5px;
		    	transform: translate(-50%, 0) rotate(-45deg);
		    }

		    &:nth-of-type(3) {
		    	transform: scaleX(0);
		    }

		    &:nth-of-type(4) {
		    	top: 50%;
		    	margin-top: -1.5px;
		    	transform: translate(-50%, 0) rotate(45deg);
		    }
		}
	}

	.lang-link {
		opacity: 0;
		pointer-events: none;
	}

	.social_item {
		&:nth-of-type(1) .social_link {
			transition-delay: 0.9s;
		}

		&:nth-of-type(2) .social_link {
			transition-delay: 0.95s;
		}

		&:nth-of-type(3) .social_link {
			transition-delay: 1s;
		}

		&:nth-of-type(4) .social_link {
			transition-delay: 1.05s;
		}

		&:nth-of-type(5) .social_link {
			transition-delay: 1.1s;
		}

		&:nth-of-type(6) .social_link {
			transition-delay: 1.15s;
		}

		&:nth-of-type(7) .social_link {
			transition-delay: 1.2s;
		}

		&:nth-of-type(8) .social_link {
			transition-delay: 1.25s;
		}

		&:nth-of-type(9) .social_link {
			transition-delay: 1.3s;
		}

		&:nth-of-type(10) .social_link {
			transition-delay: 1.35s;
		}
	}

	.social_link {
		opacity: 1;
		pointer-events: auto;
		transform: translateX(0);
	}

	.map-link {
		opacity: 1;
		pointer-events: auto;
		transform: translateX(0) scale(-1);
	}

	.visible-elem {
	    visibility: hidden;
	}

	.hidden-elem {
	    visibility: visible;
	}
}

.open-order {
	.nav_form {
		opacity: 1;
		visibility: visible;
	}
}

.open-modal {
	.nav_header {
		background-color: #fff;
		transition-delay: 0s;
	}

	.opener {
		.line {
			background-color: $color;
		}
	}

	.lang-link {
		color: $color;
	}
}

body {
	&.no-js {
		.nav_section {
			display: none;
			overflow: hidden;
			opacity: 0;
		}
	}
}

@media screen and (max-width: $desktop) {
	.logo {
		left: ($offsetd * 1.5);
        width: 25px;
        height: 35px;

		img {
            width: 25px;
            height: 35px;
		}
	}

    .menu_list {
        left: ($offsetd * 4.5);
        height: 35px;

        a {
            font-size: 1.3vw;
        }
    }


    .cases {
        .menu_list {
            display: none;
        }
    }

	.nav_header {
		width: 5vw;
	}

	.lang-link {
		font-size: 1.3vw;
	}

	.social_link {
	    width: 1.8vw;
	    height: 1.8vw;
	}

	.map-link {
	    width: 1.5vw;
	    font-size: 1.3vw;
	}

	.nav_section {
		width: 80%;
	}

	.nav_descr {
		margin-bottom: 0.5vw;
	}

	.menu_title {
		font-size: 4vw;
	}

	.closed {
	    svg {
	    	width: 2vw;
		    height: 2vw;
	    }
	}
}

@media screen and (max-width: $tablet-wide) {
	.logo {
		top: $offsetm;
		left: $offsetm;
	}

    .menu_list {
        .menu_item {
            margin-right: $offsetm;
        }

        a {
            font-size: 15px;
        }
    }

	.nav_header {
	    width: 60px;
	}

	.nav_section {
		width: calc(100% - 60px);
	}

	.nav_about,
	.nav_form,
	.nav_menu {
		width: calc(50% - 30px);
		padding: ($offsetm * 2) ($offsetm * 2) ($offsetm * 6);
	}

	.nav_row {
		&--fixed-bottom {
			padding: ($offsetm * 1.5) ($offsetm * 2);
		}
	}

	.nav_column {
		width: 100%;
		margin-top: $offsetm;
		padding-right: $offsetm;
	}

	.nav_descr {
		margin-bottom: 0;
	}

	.menu_link {
		padding-top: $offsetm;
		padding-bottom: $offsetm;
	}

	.menu_title {
		font-size: 50px;

		&::after {
			right: -5px;
			width: calc(100% + 10px);
			height: 10px;
		}
	}

	.opener {
        width: 27px;
	    height: 21px;
		margin-top: ($offsetm * 1.5);

		.line {
			height: 3px;
		}
	}

	.closed {
		top: $offsetm;
		left: ($offsetm * 2);

		svg {
		    width: 30px;
		    height: 30px;
		}
	}

	.lang {
		bottom: $offsetm;
	}

	.lang-link {
		font-size: 19px;
		margin-top: 0;
		padding: ($offsetm / 2) $offsetm;

		&:last-child {
			padding-bottom: 0;
		}
	}

	.social {
		margin-bottom: $offsetm;
	}

	.social_link {
	    width: 40px;
	    height: 40px;
	    margin: ($offsetm / 1.5);
	    padding: ($offsetm / 1.5);
	}

	.map-link {
	    font-size: 18px;
	    width: 22px;
	    margin-bottom: $offsetm;
	}
}

@media screen and (max-width: $tablet) {
	.logo {
		top: 15px;
        width: 30px;
        height: 30px;

        img {
            width: 30px;
            height: 30px;
        }
	}

    .menu_list {
        display: none;
    }

	.opener {
		position: fixed;
		top: 0;
		right: $offsetm;
        width: 23px;
        height: 19px;
        margin-top: 21px;
		pointer-events: auto;
	}

	.header {
		&::before {
			@include pseudo($pos: fixed);
			top: 0;
			left: 0;
			right: 0;
			height: 60px;
			box-shadow: -1px 0 3px rgba(25, 25, 25, 0.1);
		}
	}

	.nav_header {
		top: auto;
	    left: 0;
	    right: 0;
	    bottom: 0;
		width: auto;
		height: auto;
		flex-direction: row;
		pointer-events: none;
	}

	.nav_section {
	    flex-direction: column;
	    width: auto;
	    margin-right: 0;
	    padding-bottom: ($offsetm * 4);
	    transform: translateX(130%);
	    overflow: auto;
	    background-color: #fff;

	    &::-webkit-scrollbar {
		  width: 2px;
		  height: 0;
		}

		&::-webkit-scrollbar-track {
		  background-color: #fff;
		}

		&::-webkit-scrollbar-thumb {
		  background-color: #000;
		}
	}

    .nav_row--fixed-bottom {
        position: relative;
        padding-left: 0;
        padding-right: 0;
    }

	.map-link {
		display: none;
	}

	.social {
		flex-direction: row;
		justify-content: flex-start;
		width: 100%;
		margin-bottom: 0;
		visibility: hidden;
		transition: background 0.3s ease-out;
	}

	.lang {
		flex-direction: row;
	    top: 50%;
		left: auto;
		right: $offsetm;
	    transform: translateY(-50%);
	}

	.lang-link {
		transform: none;
		padding: $offsetm ($offsetm / 3);

		&:last-child {
			padding-right: 0;
			padding-bottom: $offsetm;
		}
	}

	.nav_about,
	.nav_form,
	.nav_menu {
		width: 100%;
		border-left: none;
		min-height: 440px;
	}

	.nav_about {
		transform: translateX(0);
		order: 1;
	}

	.nav_form {
        position: fixed;
        bottom: 0;
        justify-content: flex-start;
	    height: 100vh;
	    padding-top: ($offsetm * 4);
	    padding-bottom: ($offsetm * 5);
        overflow: auto;
		z-index: 3;

      .closed {
        position: fixed;
        top: 20px;
      }
	}

	.menu_item {
		&:first-of-type {
			.menu_link {
				padding-top: 0;
			}
		}
	}

	.open-menu {
		.nav_header {
			background-color: transparent;
			transition-delay: 0.7s;
		}

        .nav_section {
            transform: none;
        }

		.social {
			background-color: #fff;
			transition-delay: 0.65s;
		}
	}

	.open-modal {
		.nav_header {
			background-color: transparent;
		}
	}

	.case-inner,
	.about,
	.contact {
		.header {
			&::before {
				background-color: #fff;
			}
		}

		.copyright_link {
			&::before {
				@include pseudo($pos: fixed);
				left: 0;
				right: 0;
				bottom: 0;
				height: 60px;
				background-color: #fff;
				z-index: -1;
			}
		}
	}
}

@media screen and (max-width: $tablet-small) {
	.cases {
		.logo {
			filter: brightness(0) invert(1);
		}

		.opener {
			.line {
				background-color: #fff;
			}
		}

		.lang-link {
			color: #fff;
		}

		.open-menu {
			.opener {
				.line {
					background-color: $color;
				}
			}
		}
	}
}

@media screen and (max-width: $phone-wide) {
	.closed {
		top: ($offsetm * 1.5);
		left: $offsetm;
	}

	.nav_about,
	.nav_form,
	.nav_menu {
	    height: auto;
	    /*min-height: 100vh;*/
		padding: $offsetm;
	}

    .nav_about {
        margin-top: 25px;
        padding-top: 35px;
        padding-bottom: 35px;
        border-top: 1px solid rgba(25,25,25,.1);

        .nav_row--fixed-bottom {
          margin-top: 0;
        }
    }

	.nav_menu {
		padding-top: ($offsetm * 4);
	}

	.nav_form {
		padding-top: ($offsetm * 5);
		padding-bottom: ($offsetm * 2);
	}

	.nav_row {
		&--fixed-bottom {
			position: relative;
			margin-top: ($offsetm * 2);
			padding: 0;
		}
	}

	.menu_title {
		font-size: 12vw;
	}
}
