.form_column {
	@include flex(column, flex-start, stretch, nowrap);
}

select,
input:not([type="submit"]),
textarea {
	width: 100%;
	max-height: 6vw;
	padding: 0.5vw 0;
	font-family: $proxima;
	font-size: inherit;
	color: $color;
	border: none;
	border-bottom: 1px solid rgba(25, 25, 25, 0.2);
	background-color: transparent;
	outline: none;
	resize: none;
	transition: 0.1s ease-out;

	&:-webkit-autofill,
	&:-webkit-autofill:hover,
	&:-webkit-autofill:focus {
		-webkit-box-shadow: inset 0 0 0 50px #fff;
	    -webkit-text-fill-color: #000;
	}

	&.error {
		border-bottom-color: red;
	}

	@include hoverFocus {
		border-bottom-color: $color;

		&.error {
			border-bottom-color: red;
		}
	}
}

label.error,
input ~ span.error {
	color: red;
}

.wpcf7-response-output,
.screen-reader-response ul,
input[name="checkPrivacy"] {
	display: none !important;
}

[type="checkbox"] {
	& ~ label,
	& ~ span {
		@include flex(row, flex-start, center, nowrap);
		position: relative;
		width: 60%;
		cursor: pointer;

		&::before {
			content: "";
			position: relative;
			width: 1.3vw;
			height: 1.3vw;
			margin-right: 1vw;
			border: 1px solid rgba(25, 25, 25, 0.2);
			flex-shrink: 0;
			transition: 0.1s ease-out;
		}

		&::after {
			content: "";
			position: absolute;
			left: 0;
			width: 1.3vw;
			height: 1.3vw;
			background-image: url(../img/icons/icon-tick.svg);
			background-size: contain;
			transform: scale(0.2);
			opacity: 0;
			transition: 0.2s ease-out;
		}
	}

	&:checked + label,
	&:checked ~ span {
		&::after {
			transform: scale(0.5);
			opacity: 1;
		}
	}

	@include hoverFocus {
		& + label::before,
		& + span::before {
			border-color: $color;
		}
	}
}

@media screen and (max-width: $desktop) {
	[type="checkbox"] {
		& ~ label,
		& ~ span {
			width: 80%;

			&::before,
			&::after {
				width: 2vw;
				height: 2vw;
			}

			&::before {
				margin-right: $offsetd;
			}
		}
	}
}

@media screen and (max-width: $tablet-wide) {
	select,
	input:not([type="submit"]),
	textarea {
		max-height: 50px;
		padding: $offsetm 0;
	}

	[type="checkbox"] {
		& ~ label,
		& ~ span {
			width: 100%;

			&::before,
			&::after {
				width: 25px;
				height: 25px;
			}

			&::before {
				margin-right: $offsetm;
			}
		}
	}
}

@media screen and (max-width: $tablet) {
	.form_column {
		width: 100%;
	}
}
